.card {
    position: relative;
}

.card img {
    width: 100%;
    height:100%;
    display: block;
}

.card .front 
{
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position:absolute;
}

.flipped .front {

    transform: rotateY(0deg);
    transition-delay:0.2s;
}

.card .back{

    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .back
{
    transform: rotateY(90deg);
    transition-delay: 0s;
}

.flipped .matching img
{

    border: 5px;
    border-color: green;
    border-style: solid;
    border-radius: 10%;
}